import BRIEF_LIST from '~/content/meta/departmentBrief.json'

const BRIEF_MAP = BRIEF_LIST.departmentBrief.reduce((accu, org) => {
  accu[org.abbr] = org.brief
  return accu
}, {})

export default {
  props: {
    stats: {
      type: Object,
      required: true
    },
    plainText: {
      type: Boolean,
      default: false
    },
    to: {
      type: [Object, String],
      default: ''
    }
  },
  computed: {
    brief () {
      return BRIEF_MAP[this.stats.name]
    }
  }
}
