export default {
  props: {
    person: {
      type: Object,
      required: true
    },
    relatedOrgStats: {
      type: Array,
      required: true
    },
    round: {
      type: String,
      required: true
    }
  },
  computed: {
    councilorLink () {
      return {
        name: 'round-councilor-councilor',
        params: {
          round: this.round,
          councilor: this.person.id
        }
      }
    }
  },
  methods: {
    interpellationLink (catType, catValue) {
      return {
        name: 'round-councilor-councilor',
        params: {
          round: this.round,
          councilor: this.person.id
        },
        query: { catType, catValue }
      }
    }
  }
}
